/**
 * Module to help the user fix module relations
 *
 * @module     block_course_audit/autofix
 * @copyright  2025 Bastian Schmidt-Kuhl <bastian.schmidt-kuhl@ruhr-uni-bochum.de>
 * @license    http://www.gnu.org/copyleft/gpl.html GNU GPL v3 or later
 */

define(['jquery', 'core/ajax'], function($, ajax) {
    return {
        init: function() {
            $(document).on('click', '.apply-fix', function() {
                const data = {
                    moduleid: $(this).data('module-id'),
                    requiredid: $(this).data('required-id'),
                    relation: $(this).data('relation-type')
                };

                // Chain both operations: add restriction then log action
                ajax.call([{
                    methodname: 'block_course_audit_add_restriction',
                    args: data
                }])[0].then(function(response) {
                    if (response.success) {
                        // Log the successful action
                        return ajax.call([{
                            methodname: 'block_course_audit_log_action',
                            args: {
                                moduleid: data.moduleid,
                                relation: data.relation,
                                relatedto: data.requiredid
                            }
                        }])[0];
                    }
                    return Promise.reject(response.message);
                }).then(function(logResponse) {
                    console.log('Action logged:', logResponse);
                    window.location.reload();
                }).catch(function(error) {
                    console.error('Error:', error);
                });
            });
        }
    };
});